import { store } from "../../store/index";

export default (to, from, next) => {

    const isSideMenusShowing = store.getters.isSideMenusShowing
    if (isSideMenusShowing) { //MOstra os menus se nao estiver mostrando nem nao estiver no login nem registo

        store.dispatch('togleSideMenus',false)

    }

    next ()


}


