<template>
    <v-dialog v-model="dialog" width="350px">
        <v-btn
                fab
                bottom
                right
                color="pink"
                dark
                fixed
                title="Entrar"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            <v-icon>account_circle</v-icon>
            <!--<i class="fas fa-2x fa-street-view"></i>-->
        </v-btn>
        <v-card>
            <v-card-title
                    class="grey lighten-4 py-4 title"
            >
                Faca Login
            </v-card-title>
            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>
                <v-layout row wrap class="mt-5">
                    <v-flex xs12>
                        <v-text-field
                                name="email"
                                label="Mail"
                                prepend-icon="mail"
                                id="email"
                                v-model="email"
                                type="email"
                                required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                name="password"
                                label="Password"
                                prepend-icon="lock"
                                id="password"
                                v-model="password"
                                type="password"
                                required></v-text-field>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-card-actions>
                <v-btn flat color="primary">More</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
                <v-btn flat @click="onLogin">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: false,
                email: '',
                password: '',
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
        },
        methods: {
            onLogin () {
                this.$store.dispatch('loginUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            mostrarDialog () {
                if (!this.isUserAuth) {
                    this.dialog = !this.dialog
                    return
                }
                if (this.isUserAuth && confirm('Voce esta autenticado, \n\n\n\nDeseja fechar a sessao actual para iniciar uma nova sessao?')) {
                    this.$store.dispatch('logOutUser')
                    this.dialog = !this.dialog
                }
            }
        }
    }
</script>


