import * as firebase from 'firebase'
import router from '../../router/index'
import shared_store from '../auth/index'

export default {
    state: {
        userAuth: null,
        db_users: []
    },
    mutations: {
        setUserAuth (state, user) {
            state.userAuth = user
        },
        setDBUser (state, users) {
            state.db_users = users
        },
    },
    getters: {
        userAuth (state) {
            return state.userAuth
        },
    },
    actions: {

        createUser ({commit}, value) {
            commit('setLoading',true)
            commit('clearError')
            let userOjectt = {}
            firebase.auth().createUserWithEmailAndPassword(value.email, value.password)
                .then(
                    user => {
                        userOjectt = {
                            id: user.uid,
                            name: '',
                            idade: -1,
                        }
                        // Gravar o novo user no firebase
                        return firebase.database().ref('Users').push(userOjectt)
                    }
                )
                .then(() => {
                    commit('setUserAuth',userOjectt)
                    commit('setLoading',false)
                })
                .catch(
                    error => {
                        commit('setLoading',false)
                        commit('setError',error)
                        console.log(error)
                    }
                )

        },

        loginUser ({commit}, value) {
            commit('setLoading',true)
            commit('clearError')
            firebase.auth().signInWithEmailAndPassword(value.email, value.password)
                .then(
                    user => {
                        const newUser = {
                            id: user.uid,
                        }
                        commit('setUserAuth', newUser)

                        // Falta encontrarmos uma forma de fechar a dialog

                        router.push('/teste/profile')
                    }
                )
                .catch(
                    error => {
                        commit('setLoading',false)
                        commit('setError',error)
                        console.log(error)
                    }
                )

        },

        logOutUser () {
            firebase.auth().signOut()
            router.push('/')
        },
        autoSignIn ({commit}, user) {
            commit('setUserAuth', user)
        },
        getAllUsers ({commit,state}) {

            firebase.database().ref('Users').once('value')
                .then((data) => {
                    const userss = []
                    const obj = data.val()
                    for (let key in obj) {
                        userss.push({
                            id: obj[key].id,
                            idade: obj[key].idade,
                            name: obj[key].name,
                        })
                    }
                    commit('setDBUser',userss)
                    return userss
                })
                .then( list => {
                    if (state.userAuth === null || state.userAuth === undefined) return
                    const user = list.find(user => {
                        return user.id === state.userAuth.uid
                    })
                    commit('setUserAuth',user)
                })
                .catch( (error => {
                    console.log(error)
                }))
        },

    }
}


